'use client';

import { useState } from 'react';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';

export default function ImageService() {
  const [file, setFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string>('');
  const [uploadedUrl, setUploadedUrl] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [isUploading, setIsUploading] = useState(false);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    setError('');
    setUploadedUrl('');

    if (selectedFile) {
      if (!selectedFile.type.startsWith('image/')) {
        setError('Please select a valid image file');
        return;
      }

      setFile(selectedFile);
      const objectUrl = URL.createObjectURL(selectedFile);
      setPreviewUrl(objectUrl);
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setError('Please select an image to upload');
      return;
    }

    setIsUploading(true);
    setError('');

    const formData = new FormData();
    formData.append('file', file);

    try {
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Upload failed');
      }

      const data = await response.json();
      setUploadedUrl(data.url);
    } catch (err: any) {
      setError(err.message || 'Failed to upload image');
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <main className="min-h-screen bg-zinc-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-3xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-zinc-900 mb-4">
            Image Service
          </h1>
          <p className="text-lg text-zinc-600 mb-4">
            Upload and store your images securely in the cloud
          </p>
          <div className="flex gap-4 justify-center flex-wrap mb-8">
            <div className="relative w-[200px] h-[200px]">
              <img
                src="/api/image/200/200"
                alt="Random image 200x200"
                className="rounded-lg shadow-lg w-full h-full object-cover"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.style.display = 'none';
                  target.nextElementSibling?.classList.remove('hidden');
                }}
              />
              <div className="hidden absolute inset-0 flex items-center justify-center bg-zinc-100 rounded-lg p-4 border-2 border-dashed border-zinc-200">
                <p className="text-sm text-zinc-600 text-center">
                  Upload some images to see random resized versions here
                </p>
              </div>
            </div>
            <div className="relative w-[300px] h-[200px]">
              <img
                src="/api/image/300/200"
                alt="Random image 300x200"
                className="rounded-lg shadow-lg w-full h-full object-cover"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.style.display = 'none';
                  target.nextElementSibling?.classList.remove('hidden');
                }}
              />
              <div className="hidden absolute inset-0 flex items-center justify-center bg-zinc-100 rounded-lg p-4 border-2 border-dashed border-zinc-200">
                <p className="text-sm text-zinc-600 text-center">
                  Upload some images to see random resized versions here
                </p>
              </div>
            </div>
            <div className="relative w-[200px] h-[300px]">
              <img
                src="/api/image/200/300"
                alt="Random image 200x300"
                className="rounded-lg shadow-lg w-full h-full object-cover"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.style.display = 'none';
                  target.nextElementSibling?.classList.remove('hidden');
                }}
              />
              <div className="hidden absolute inset-0 flex items-center justify-center bg-zinc-100 rounded-lg p-4 border-2 border-dashed border-zinc-200">
                <p className="text-sm text-zinc-600 text-center">
                  Upload some images to see random resized versions here
                </p>
              </div>
            </div>
          </div>
        </div>

        <Card className="p-8 bg-white shadow-xl rounded-xl border-0">
          <div className="space-y-8">
            <label 
              htmlFor="file-upload"
              className="flex flex-col items-center justify-center border-2 border-dashed border-zinc-300 rounded-xl p-8 cursor-pointer hover:border-zinc-400 hover:bg-zinc-50 transition-all duration-200"
            >
              <input
                type="file"
                accept="image/jpeg,image/png,image/gif,image/webp"
                onChange={handleFileChange}
                className="hidden"
                id="file-upload"
                aria-label="Upload image"
              />
              {previewUrl ? (
                <div className="relative w-full max-w-md">
                  <img
                    src={previewUrl}
                    alt="Preview"
                    className="w-full h-auto rounded-lg shadow-md"
                  />
                  <p className="mt-4 text-sm text-zinc-500 text-center">
                    Click to change image
                  </p>
                </div>
              ) : (
                <div className="text-center">
                  <svg
                    className="mx-auto h-16 w-16 text-zinc-400"
                    stroke="currentColor"
                    fill="none"
                    viewBox="0 0 48 48"
                    aria-hidden="true"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                    />
                  </svg>
                  <p className="mt-4 text-zinc-600">
                    Click to select an image
                  </p>
                  <p className="mt-2 text-sm text-zinc-500">
                    PNG, JPG, GIF up to 10MB
                  </p>
                </div>
              )}
            </label>

            {error && (
              <div className="p-4 bg-red-50 rounded-lg">
                <p className="text-red-600 text-center text-sm">{error}</p>
              </div>
            )}

            {uploadedUrl && (
              <div className="p-4 bg-emerald-50 rounded-lg text-center space-y-2">
                <p className="text-emerald-600 font-medium">Upload successful!</p>
                <a
                  href={uploadedUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-emerald-700 hover:text-emerald-800 underline text-sm"
                >
                  View uploaded image
                </a>
              </div>
            )}

            <Button
              onClick={handleUpload}
              disabled={!file || isUploading}
              className="w-full bg-zinc-900 hover:bg-zinc-800 text-white py-6 text-lg font-medium rounded-lg transition-colors duration-200"
            >
              {isUploading ? 'Uploading...' : 'Upload Image'}
            </Button>
          </div>
        </Card>
      </div>
    </main>
  );
}
